:root {
  --hover-color: #00acdc;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.logo img {
  width: 40px;
  height: 40px;
  margin-top: 10px;
  margin-bottom: 10px;
  display: inline-block;
}

nav {
  display: inline-block;
}

nav ul {
  list-style: none;
  display: flex;
}

nav li:not(:first-child) {
  margin-left: 20px;
}

nav a {
  color: #000;
  text-transform: uppercase;
  text-align: center;
  gap: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

nav a.is-active, a:hover {
  color: var(--hover-color);
}

nav a.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
